.mainUserDataContainer {
  text-align: center;
  color: white;
}

.userPicture {
  padding-top: 3rem;
}

h1 {
  font-size: 2.2rem;
  padding-top: 1rem;
  width: 95%;
  margin: 0 auto;
}
h2 {
  padding-top: 1rem;
  width: 95%;
  margin: 0 auto;
}
h2 a {
  font-size: 2rem;
  margin: 1rem 0;
  color: teal;
  text-decoration: none;
  border:1px solid transparent;
  padding: 0.5rem

}
 .mainUserDataContainer a:hover {
  border: 3px solid teal;
  border-radius: 7px;
}

p {
  font-size: 1.2rem;
}

.userPicture > img {
  width: 150px;
  height: 150px;
  border-radius: 10%;
}
.userStatistics {
  display: flex;
  justify-content: space-between;
  max-width: 350px;
  margin: 0 auto;
  width: 95%;
}
.statBox {
  margin:0.2rem;
  border-radius: 5%;
  background-color: rgb(35, 35, 36);
  padding: 0.5rem;
  min-width: 90px;
}
.statBox * {
  display: block;
}
.statBoxNum {
  font-size: 1.3rem;
}
.statBoxLabel {
  padding-top: 0.7rem;
  font-size: 0.8rem;
}
